@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@300&family=Hanken+Grotesk:ital,wght@0,200;0,300;0,400;1,100&display=swap');

#root {
  text-align: center;
  background-color: black;
}

#welcomeDiv {
  min-width: 90%;
  min-height: 100vh;
  padding: 0 5% 0 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: white;
  font-weight: 600;
}

#greetingDiv {
  position: relative;
  display: flex;
  flex-direction: column;
  color: orange;
  font-size: 6em;
  font-weight: 800;
  text-align: end;
  padding-right: 2.5%;
  min-width: 50%;
  min-height: 100vh;
  align-items: flex-end;
  justify-content: center;
  animation: moveInLeft 3s ease-out;
}

#greetingDiv>div:nth-of-type(even) {
  font-size: 0.8em;
}

#welcomeDivInner {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 1.75em;
  text-align: start;
  min-width: 50%;
  padding-left: 2.5%;
  min-height: 100vh;
  align-items: flex-start;
  justify-content: center;
  animation: moveInRight 3s ease-out;
}

#welcomeMessDiv>div:nth-of-type(even) {
  font-size: 0.75em;
}

#authorDiv {
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  font-size: 1.25em;
  font-weight: 800;
  /* color: rgb(128, 206, 240); */
  color: orange;
}

