} */

/* ---------- */
#mainPage {
  display: none;
  min-width: 100wh;
  min-height: 100vh;
  background-color: black;
}

#sideCard {
  display: flex;
  flex-direction: column;
  margin: 2.5%;
  color: white;
  padding-left: 2%;
  text-align: left;
  border-right: 1px solid orange;
  border-radius: 5%;
  align-items: flex-start;
  justify-content: flex-start;
  min-width: 20%;
  max-width: 20%;
}

#sideCardImg > img {
  max-width: 85%;
  border-radius: 10% 10% 10% 0;
}

#sideCardAuthor {
  color: orange;
  font-size: 1.2em;
  font-weight: 800;
}

#sideCardLocation {
  margin-top: 5%;
  font-weight: 700;
}

#sideCardLocation img {
  max-width: 8%;
  margin-right: 2%;
}

#sideCardSocials {
  margin-top: 5%;
  font-weight: 500;
  font-size: 0.8em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  max-width: 100%;
}

#sideCardSocials > div {
  min-width: 33%;
  max-width: 33%;
  /* margin: 2.5% 0; */
}

#sideCardSocials img {
  min-width: 33%;
  max-width: 33%;
}

#mainCard {
  display: flex;
  flex-direction: column;
  color: orange;
  padding: 5%;
}

#mainCardCredo {
  display: flex;
  flex-direction: row;
  font-size: 2em;
}

#mainCardCredo > div:nth-of-type(even) {
  color: white;
}

#mainCardDesc {
  padding: 5%;
  color: white;
  font-size: 1.2em;
  font-weight: 400;
  font-family: 'Cormorant', serif;
  font-family: 'Hanken Grotesk', sans-serif;
}

#mainCardResearchInterests {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-bottom: 5%;
  font-family: 'Cormorant', serif;
  font-family: 'Hanken Grotesk', sans-serif;
}

.mainCardResearchInterestsBox {
  display: flex;
  flex-direction: column;
  color: white;
  min-width: 90%;
  max-width: 90%;
  padding: 5%;
  border-bottom: 1px solid white;
}

.mainCardResearchInterestsBoxDiv {
  font-size: 1.5em;
  font-weight: 800;
  padding-bottom: 3%;
  color: orange;
}

.mainCardResearchInterestsBoxDiv:nth-of-type(even) {
  font-size: 0.75em;
  color: white;
}

#mainCardPaperTakeaways {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-bottom: 5%;
  font-family: 'Cormorant', serif;
  font-family: 'Hanken Grotesk', sans-serif;
}

.mainCardPaperTakeawaysBox {
  display: grid;
  flex-direction: column;
  flex-wrap: wrap;
  color: white;
  padding: 5%;
  border-bottom: 1px solid white;
}

.mainCardPaperTakeawaysBoxDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 1.5em;
  font-weight: 800;
  color: orange;
  min-width: 100%;
  max-width: 100%;
  min-height: 30vh;
}

.mainCardPaperTakeawaysBoxDivTopInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  font-size:0.5em;
  min-width: 70%;
  max-width: 70%;
}

.mainCardPaperTakeawaysBoxDivTopInnerNavBtn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  font-size:0.5em;
  min-width: 15%;
  max-width: 15%;
}

.mainCardPaperTakeawaysBoxDiv a, a:active {
  color: orange;
  text-decoration: none;
}

.mainCardPaperTakeawaysBoxDiv a:hover {
  color: white;
}

.mainCardPaperTakeawaysBoxDivTitle {
  font-size: 1.2em;
  padding: 1%;
}

.mainCardPaperTakeawaysBoxDivAuthor {
  font-size: 1em;
  font-weight: 600;
  padding-bottom: 3%;
  color: white;
  font-size: 1em;
}

#mainCardPaperTakeawaysBoxDivTopInnerNEXT:hover, #mainCardPaperTakeawaysBoxDivTopInnerPREV:hover {
  color: white;
}

.mainCardPaperTakeawaysBoxDiv:nth-of-type(even) {
  font-size: 0.75em;
  color: white;
  padding-bottom: 3%;
}

.mainCardPaperTakeawaysBoxDivInner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  min-width: 40%;
  max-width: 40%;
  padding: 1% 5%;
  font-size: 1em;
}

.mainCardPaperTakeawaysBoxDivInnerDiv {
  color: white;
}

.mainCardPaperTakeawaysBoxDivInnerDiv:nth-of-type(odd) {
  font-size: 0.9em;
  font-weight: 700;
  color: orange;
  padding-bottom: 1%;
}

#mainCardGithub {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 5%;
  font-family: 'Cormorant', serif;
  font-family: 'Hanken Grotesk', sans-serif;
}

.mainCardGithubBox {
  display: flex;
  flex-direction: column;
  color: white;
  min-width: 35%;
  max-width: 35%;
  padding: 5%;
  border-bottom: 1px solid white;
}

.mainCardGithubBox > div {
  font-size: 1.5em;
  font-weight: 800;
}

.mainCardGithubBox > div:nth-of-type(even) {
  font-size: 0.75em;
}

.mainCardGithubBox a, a:active {
  color: orange;
  text-decoration: none;
}

.mainCardGithubBox a:hover {
  color: white;
}

#history {
  display: flex;
  flex-direction: row;
  min-width: 80%;
  max-width: 80%;
}

.timeline {
  min-width: 60%;
  max-width: 60%;
  color: white;
  display: flex;
  flex-direction: column;
}

.eduHistoryItem {
  display: flex;
  flex-direction: column;
  margin: 3%;
  font-size: 0.75em;
}

.eduHistoryItem > div:first-of-type {
  color: orange;
  font-size: 1.5em;
  font-weight: 600;
}

@keyframes moveInLeft {
  0% {
      /* margin-right: 100%; */
      transform: translateX(-100%);
  }
  80% {
      transform: translateX(2.5%);
  }
  100% {
      margin-right: 0%;
      transform: translateX(0%);
  }
}

@keyframes moveInRight {
  0% {
      transform: translateX(100%);
  }
  80% {
      transform: translateX(-2.5%);
  }
  100% {
      transform: translateX(0%);
  }
}

@keyframes fadeIntoOblivion {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0;
  }
}

@media (max-width: 1000px) {
  #welcomeDiv {
    flex-direction: column;
    justify-content: center;
    max-height: 100vh;
    overflow: hidden;
  }

  #greetingDiv {
    min-height: 0;
    max-height: none;
    /* margin: 0; */
    font-size: 3em;
    animation: moveInTop 3s ease-out;
  }

  /* #greetingDiv>div:nth-of-type(even) {
    font-size: 0.8em;
  } */
  
  #welcomeDivInner {
    min-height: 0;
    max-height: none;
    margin: 5% 0 0 0;
    font-size: 0.875em;
    animation: moveInBottom 3s ease-out;
  }
  
  /* #welcomeMessDiv>div:nth-of-type(even) {
    font-size: 0.75em;
  } */

  #mainPage {
    flex-direction: column;
  }

  #sideCard {
    min-width: 95%;
    border: none;
    padding-bottom: 5%;
    border-bottom: 1px solid orange;
  }

  #sideCardImg, 
  #sideCardAuthor, 
  #sideCardAuthorInTamil,
  #sideCardLocation > div {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  #sideCardSocials > div > a {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  #sideCard > div {
    min-width: 100%;
    max-width: 100%;
  }

  #sideCardImg > img {
    max-width: 30%;
  }

  .mainCardGithubBox {
    min-width: 90%;
    max-width: 90%;
  }

  #history, .timeline {
    min-width: 100%;
    flex-direction: column;
  }

  .mainCardPaperTakeawaysBoxDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
    flex-wrap: wrap;
    font-size: 1.5em;
    font-weight: 800;
    color: orange;
  }

  .mainCardPaperTakeawaysBoxDivInner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: left;
    min-width: 100%;
    max-width: 100%;
    padding: 1% 5%;
    font-size: 1em;
  }

  #mainCardPaperTakeawaysBoxDivTopInnerNEXT {
    padding-bottom: 5%;
  }

  @keyframes moveInTop {
    0% {
        /* margin-right: 100%; */
        transform: translateY(-300%);
    }
    80% {
        transform: translateY(5%);
    }
    100% {
        margin-right: 0%;
        transform: translateY(0%);
    }
  }

  @keyframes moveInBottom {
    0% {
        transform: translateY(300%);
    }
    80% {
        transform: translateY(-5%);
    }
    100% {
        transform: translateY(0%);
    }
  }

}
